//=================================================TYPOHRAPHY================================================//

$font-main: 'DM Sans', sans-serif; //primary font
$font-secondary: 'Titan One', cursive; //secondary font, mostly used in section headings
$font-size: 14px; //seems to be most common size, at least for text in product cards
$line-height: 1.5; //the same reason, look font-size :)
$letter-spacing: 0.04em; //it occurs quiet often, so it has to be in distinct variable

//===================================================COLORS AND HUES========================================//

//brand colors and hues
$clr-brand: #ffb8ca; //brand color of the project;
$clr-brand-darker: #ffa5ba; // a bit darker version of brand color
$clr-brand-the-darkest: #e17992; //the darkest version of brand color

//accent colors and hues
$clr-accent: #d41443; // accent color, is used for section headings and hover, focus effects
$clr-accent-darker: #c7133e; // darker version of accent color

//text colors hues
$clr-text-normal: #907e82; // most commont text color
$clr-text-darker: #000000; // color to darken a text
$clr-text-lighter: #ffffff; // color to lighter a text

//background colors and hues
$clr-bg-normal: #fff5f6; // most common background color
$clr-bg-lighter: #fafafa; // lighter version of background
$clr-bg-the-lightest: #ffffff; // the lightest virsion of background

$clr-bg-bright-01: #f0d1a5; // color for bright background in PRODUCT section and OUR LOCATION version 01
$clr-bg-bright-02: #c2e297; //color for bright background in PRODUCT section and OUR LOCATION version 02

//decoration colors
$clr-dots-cstm-rev: #d9d9d9; // color for dots that indicate switching to another comment in CUSTOMER REVIEW section
$clr-undrline-effect: #e1e1e1; //is used for underlined effect in OUR LOCATION
